import { query } from './query'
// 
export function addCategory (data, cb) {
  query('addCategory', data, cb, 'goods')
}// 
export function listCategory (data, cb) {
  query('listCategory', data, cb, 'goods', 'get')
}
// 
export function delCategory (data, cb) {
  query('delCategory', data, cb, 'goods')
}

